import { Component, OnInit, } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
  import Chatkit from '@pusher/chatkit-client';
  import { FormsModule } from '@angular/forms';
  import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-user',
  templateUrl: './user.component.html',
  styleUrls: ['./user.component.css']

})
export class UserComponent implements OnInit {
  public itemid: string;
  constructor( private activatedRoute: ActivatedRoute )
  {
    debugger;
    this.userId = this.activatedRoute.snapshot.params["itemid"];
       
    this.addUser();
  }
  userId = '';
  currentUser = <any>{};
  messages = [];
  currentRoom = <any>{};
  roomUsers = [];
  userRooms = [];
  newMessage = '';
  newRoom = {
    name: '',
    isPrivate: false
  };
  newUser = '';
  joinableRooms = [];

  connectToRoom(id) {
    this.messages = [];
    const { currentUser } = this;
    currentUser.subscribeToRoom({
      roomId: `${id}`,
      messageLimit: 100,
      hooks: {
        onMessage: message => {
          this.messages.push(message);
        },
        onPresenceChanged: () => {
          this.roomUsers = this.currentRoom.users.sort((a) => {
            if (a.presence.state === 'online') return -1;

            return 1;
          });
        },
      },
    })
    .then(currentRoom => {
      this.currentRoom = currentRoom;
      this.roomUsers = currentRoom.users;
      this.userRooms = currentUser.rooms;
    });
  }

  addUser() {     
    debugger; 
    const { userId } = this;
    const tokenProvider = new Chatkit.TokenProvider({
      url: 'https://us1.pusherplatform.io/services/chatkit_token_provider/v1/e79c9366-b586-4d8d-81ba-dec44fe56126/token'
    });

    const chatManager = new Chatkit.ChatManager({
      instanceLocator: "v1:us1:e79c9366-b586-4d8d-81ba-dec44fe56126",
      userId,
      tokenProvider
    });

    chatManager
    .connect()
    .then(currentUser => {
      this.currentUser = currentUser;
            this.connectToRoom(currentUser.rooms[0].id);
            
    })
    .catch(error => {
        console.error("error:", error);
    });
  }
  sendMessage() {
    const { newMessage, currentUser, currentRoom } = this;

    if (newMessage.trim() === '') return;

    currentUser.sendMessage({
      text: newMessage,
      roomId: `${currentRoom.id}`,
    });

    this.newMessage = '';
  }
  createRoom() {
    const { newRoom: { name, isPrivate }, currentUser } = this;

    if (name.trim() === '') return;

    currentUser.createRoom({
      name,
      private: isPrivate,
    }).then(room => {
      this.connectToRoom(room.id);
      this.newRoom = {
        name: '',
        isPrivate: false,
      };
    })
    .catch(err => {
      console.log(`Error creating room ${err}`)
    })
  }
  getJoinableRooms() {
    const { currentUser } = this;
    currentUser.getJoinableRooms()
    .then(rooms => {
      this.joinableRooms = rooms;
    })
    .catch(err => {
      console.log(`Error getting joinable rooms: ${err}`)
    })
  }

  joinRoom(id) {
    const { currentUser } = this;
    currentUser.joinRoom({ roomId: id })
    .catch(err => {
      console.log(`Error joining room ${id}: ${err}`)
    })
  }
  addUserToRoom() {
    const { newUser, currentUser, currentRoom } = this;
    currentUser.addUserToRoom({
      userId: newUser,
      roomId: currentRoom.id
    })
      .then((currentRoom) => {
        this.roomUsers = currentRoom.users;
      })
      .catch(err => {
        console.log(`Error adding user: ${err}`);
      });

    this.newUser = '';
  }
  ngOnInit() {
  }

}

